/*======== 2.13 MODALS ========*/
.modal {
	.modal-lg {
		@include desktops {
		    max-width: 870px!important;
		}
	}
}
.modal.show {
	.modal-dialog {
	    @include transform(translate(0,15%));
		@include desktops {
	    	@include transform(translate(0,25%));
		}
	}
}
.modal-content {
	@include box-shadow(0px, 3px, 12px, rgba(0, 0, 0, 0.08));
	border: none;
	.modal-item {
		padding: 15px;
		@include desktops {
			padding: 30px;
		}
	}
}
button.close {
    padding: 0;
    cursor: pointer;
    background: 0 0;
    border: 0;
    font-size: 16px;
    -webkit-appearance: none;
    position: absolute;
    right: 15px;
    z-index: 55;
    &:hover, &.active, &:focus {
    	outline: none;
    	box-shadow: none;

    }
}
.card_img {
	position: relative;
	overflow: hidden;
}

