/*======== 2.1 ACCORDIONS & TOGGLES ========*/ 
.accordion {
  .card {
    margin-bottom: 10px;
  }
  .card-header {
    padding: 0px;
    border-radius: 0px;
    border: none;
    h6 {
      position: relative;
      i{
        margin-right: 5px;
      }
    }
    [data-toggle=collapse] {
      display: block;
      padding: 15px 20px;
      line-height: 24px;
      background-color: $primary-color;
      color: $white-color;
      border: 1px solid $primary-color;
      border-radius: 2px;
    }
    [data-toggle=collapse]:after {
      font-family: FontAwesome;
      font-style: normal;
      speak: none;
      content: '\f106';
      float: right;
      font-size: 20px;
      text-align: center;
      right: 15px;
      @include transition(transform 0.3s ease);
    }
    [data-toggle=collapse].collapsed {
      line-height: 24px;
      color: $title-color;
      background-color: $sand;
      border: 1px solid #e5e5e5;
    }
    [data-toggle=collapse].collapsed:after {
      @include transform(rotate(180deg));
    }
  }
  .card-block {
    border: 1px solid #e5e5e5;
    border-top: 0px;
    padding: 30px 20px;
    font-size: 15px;
    line-height: 26px;
  }
}
