/*======== 2.9 FORMS ========*/ 
.form-group {
  margin-bottom: 0px;
}
.input-group-addon {
  border-color: $sand;
}
select.form-control:not([size]):not([multiple]) {
  height: unset;
}
input.form-control, textarea.form-control, select.form-control {
  border-radius: 0px;
  font-size: 14px;
  padding: 12px 20px;
  line-height: 24px;
  border: 1px solid $border-color;
  background-color: $white-color;
  margin-bottom: 30px;
  font-weight: 300;
  @include placeholder {
    color: $text-color !important;
  }
  &.focus, &:focus, &.active, &:active {
    border-color: $primary-color;
    font-weight: 300; 
  }
}
input.form-control.is-valid, input.form-control.is-invalid {
  &.focus, &:focus, &.active, &:active {
    background-color: $white-color;
    border-color: default;
    font-weight: 300;
    //@include box-shadow(0px, 2px, 7px, rgba( 0, 0, 0, 0.05));
    box-shadow: default;
  }
}
form {
  button[type="submit"] {

  }
}

.form-group {
  margin-bottom: 10px;
  @include tablet {
    margin-bottom: 25px;
  }
  label {
    font-size: 15px;
    font-weight: 400;
    line-height: 40px;
    color: $title-color;
    @include desktops {
      text-align: right;
      font-weight: 500;
      font-size: 16px;
    }
  }
}
input.form-control, textarea.form-control, select.form-control {
  margin-bottom: 0px;
}


// SelectBox input style ==========================================
.form-group {
  .helpText {
    position: absolute;
    top: 18px;
    right: 25px;
  }
}
.selectOptions {
  margin-bottom: 0px;
  .sbHolder {
    background-color: transparent;
    border: none;
    margin: 0;
    border-radius: 0;
    border: 1px solid $border-color;
    a.sbSelector {
      color: $text-color!important;
      &:hover, &:focus, &:active {
        color: $primary-color!important;    
      }
    }
  }
  .sbToggle {
    color: $text-color!important;
  }
  .sbToggle:hover {
    color: $primary-color!important;    
  }
  .sbHolder, .sbSelector {
    width: 100% !important;
    height: 50px !important;
    line-height: 50px;
  }
  .sbOptions {
    width: 100% !important;
    background-color: $white-color;
    border: 1px solid $border-color;
    right: 0 !important;
    border-radius: 0;
    z-index: 100;
    a {
      color: $text-color!important;
      font-size: 13px;
      padding: 13px 17px;
      text-transform: uppercase;
      &:hover {
        color: $primary-color!important;
        background-color: transparent;
      }
    }
  }
  .sbHolder > a {
    &.sbToggle:after {
      position: absolute;
      content: '\f0d7';
      font-family: 'FontAwesome';
      top: 0px;
      right: 15px;
      font-size: 16px;
    }
    &.sbToggle.sbToggleOpen:after {
      content: '\f0d8';
    }
  }
}
// Checkbox & Radio Buttons styling ==========================================
.form-check-input {
  margin-left: 4px;
  margin-top: 6px;
  padding: 5px;
  opacity: 0;
}
.form-check-label {
  padding-left: 0px;
  margin-bottom: 25px;
  font-size: 15px;
  cursor: default;
  color: $title-color;
}
.checkbox-custom + .checkbox-custom-label:before, .radio-custom + .radio-custom-label:before {
  content: '';
  background: $white-color;
  border: 1px solid #dedede;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  padding: 0;
  margin-right: 10px;
  text-align: center;
  line-height: 20px;
}
.checkbox-custom:checked + .checkbox-custom-label:before {
  content: "\f00c";
  font-family: 'FontAwesome';
  color: $white-color;
  background: $primary-color;
  border: 0px solid $primary-color;
}
.radio-custom + .radio-custom-label:before {
  border-radius: 50%;
}
.radio-custom:checked + .radio-custom-label:before {
  content: "\f111";
  font-size: 9px;
  font-family: 'FontAwesome';
  color: $white-color;
  background: $primary-color;
  border: 0px solid $primary-color;
}
span.or {
  font-size: 15px;
  padding: 15px 0;
  display: block;
  color: #222222;
}

.checkbox-custom:checked + .checkbox-custom-label,
.radio-custom:checked + .radio-custom-label {
  &.default-checked:before {
    background-color: #e5e5e5!important;
    color: $text-color!important;
  }
  &.disable-checked:before {
    background: #f2f2f2!important;
    color: tint($text-color, 50%)!important;
  }
}


