.checkout-area {
  margin-bottom: 15px;
  .form-check {
    margin-bottom: 25px;
    label {
      margin-bottom: 0px;
    }
    input {
      display: block; 
    }
    span {
      margin-left: 32px;
      line-height: 24px;
      font-size: 14px;
      color: $text-color;
      font-family: 'Open Sans', sans-serif;
    }
  }
  .checkbox-custom + .checkbox-custom-label:before, .radio-custom + .radio-custom-label:before {
    line-height: 13px;
    width: 17px;
    height: 17px;
    border-width: 2px;
    margin-right: 15px;
  }
  .radio-custom:checked + .radio-custom-label:before {
    content: "\f00c";
    font-size: 10px;
    font-family: 'FontAwesome';
    color: $primary-color;
    background: transparent;
    border: 2px solid $primary-color;
  }
}
// Steps Pagination ================== 
.steps_progress-bar {
  margin-bottom: 25px;
  .step {
    color: $text-color;
    display: inline-block;
    position: relative;
    padding: 0 5px;
    font-size: 12px;
    @include mobile {
      padding: 0 10px;
      font-size: 12px; 
    } 
    @include tablet {
      padding: 0 40px;
      font-size: 15px;
    }
  }
  .step a {
    color: inherit;
    font-weight: 500;
  }
  .step span {
    font-style: normal;
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 28px;
    border: 1px solid $border-color;
    text-align: center;
    font-weight: 600;
    margin-right: 5px;
    @include mobile {
      margin-right: 15px;
    } 
    @include tablet {
      width: 40px;
      height: 40px;
      margin-right: 15px;
      line-height: 38px;
    }
  }
  .step.active, .step:hover {
    color: $primary-color;;
  }
  .step:first-child {
    padding-left: 0px;
  }
  .step.active span {
    background: $primary-color;;
    color: $white-color;
  }
  .step.completed {
    color: $primary-color;
    span {
      border-color: $primary-color;;
      color: $primary-color;
    }
  }
  .step:after {
    position: absolute;
    content: '\f105';
    font-family: FontAwesome;
    top: 6px;
    right: -6px;
    font-size: 20px;
    color: rgba($primary-color , 0.2);
    @include tablet {
      top: 10px;
      right: -7px;
      font-size: 34px;
    }
  }
  .step:last-child {
    padding-right: 0px;
    &:after {
      content: '';
    }
  }
}
.option-title {
  border-bottom: 1px solid $border-color;
  margin-bottom: 25px;
  margin-top: 15px;
  h5 {
    padding-bottom: 15px;
  }
  .edit {
    border: none;
    background: transparent;
    font-size: 16px;
    text-transform: capitalize;
    color: $primary-color;
    cursor: pointer;
    i {
      margin-right: 5px;
    }
  }
}
.info {
  font-size: 14px;
  color: $text-color;
  font-family: 'Open Sans', sans-serif;
}
// Checkout form =============
.checkout form {
  .form-group label {
    color: #222;
    font-weight: 500;
    font-family: 'Open Sans', sans-serif;
    text-align: left;
  }
}
.continue {
  border: 1px solid $border-color;
  padding: 20px;
}
// Order summery ================
.cart_summery-box {
  padding: 50px 30px;
  h5 {
    margin-bottom: 5px;
  }
  p {
    margin-bottom: 15px;
  }
  ul li {
    font-family: 'Open Sans', sans-serif;
    padding: 15px 0;
    font-size: 15px;
    border-bottom: 1px solid $border-color;
    &:last-child {
      color: $title-color;
      font-weight: 500;
      border-bottom: 1px solid transparent;
      padding-bottom: 0px;
    }
  }
}
// Payment Option ================
.payment-method {
  margin-bottom: 50px;
  .payment-method-card {
    display: block;
    margin-top: 10px;
    padding-left: 32px;
    li {
      width: auto;
      margin-right: 5px;
      display: inline-block;
      img {
        max-width: 100%;
      }
      &:last-child {
        margin-right: 0px;
      }
    }
    .form-group {
      margin-bottom: 15px;
      label {
        font-size: 14px;
        font-weight: 600;
      }
    }
  }
}
.btn-pager {
  color: $text-color;
  border: 1px solid;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
  border-color: $border-color;
  &:hover {
    border-color: $primary-color;
    background-color: $primary-color;
    color: $white-color;
  }
  &.next {
    border-color: $primary-color;
    background-color: $primary-color;
    color: $white-color;
  }
}
// Confirmation ================
.confirmation-text {
  h2 {
    font-size: 28px;
    margin-bottom: 25px;
  }
  p {
    margin-bottom: 15px;
  }
  .btn {
    margin: 20px 5px;
  }
}