/*======== Sidebar contents style ========*/
.form_search {
  margin-bottom: 40px;
  .btn-search {
    background: transparent;
    border: none;
    position: absolute;
    top: 16px;
    right: 15px;
    color: $text-color;
    cursor: pointer;
  }
}

.sidebar_title {
  padding-top: 0px;
  padding-bottom: 35px;
  text-transform: uppercase;
}
.sidebar {
  .list-group {
    a {
      display: block;
      color: $text-color;
      &:hover {
        color: $title-color;
      }
    }
    li {
      padding: 10px 0;
    }
    li:first-child {
      padding-top: 0px;
    }
    li:last-child {
      padding-bottom: 0px;
    }
  }
  .sidebar_block-category, .sidebar_block-price, .sidebar_block-size {
    margin-bottom: 45px;
  }
  a.active {
    color: $primary-color;
    .square {
      background-color: $primary-color;
      border-color: $primary-color;
    }
  }
}
.sidebar_latest-post {
  padding-bottom: 30px;
  .rd_media {
    margin-bottom: 20px;
  }
  .rd_media-right {
    .media-title {
      padding-bottom: 5px;
      line-height: 1.2;
    }
    p {
      line-height: 1;
      font-size: 14px;
    }
  }
}
// Price Range ===============
.price-slider-inner {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  overflow: visible;
  .amount-wrapper {
    display: inline-block;
    text-align: right;
    max-width: 50%;
  }
  input {
    display: inline-block;
    max-width: 40%;
    text-align: center;
    color: $text-color;
    border: none;
    font-weight: 500;
  }
  .btn {
    font-size: 13px;
    padding: 0 15px;
  }
}

.priceRange .ui-widget-content {
  background: none;
  border: 0;
  background-color: #f0f0f0;
  height: 4px;
  clear: both;
  border-radius: 0;
  margin: 0px 7px 30px 10px;
}
.ui-widget-content .ui-slider-handle {
  border-radius: 50%;
  background: none;
  border: 3px solid #252525;
  background-color: #ffffff;
  top: -6px;
  width: 16px;
  height: 16px;
  outline: none;
}
.ui-widget-content .ui-slider-range {
  background-color: #252525;
  border: none;
  border-radius: 0;
}
// 
.square {
  width: 12px;
  height: 12px;
  display: inline-block;
  margin-right: 8px;
  border: 1px solid $border-color;
}
i.color1 {
  background:Black!important;
  border-width: 0px;
}
i.color2 {
  background: Purple!important;
  border-width: 0px;
}
i.color3 {
  background: Brown!important;
  border-width: 0px;
}
i.color4 {
  background: Red!important;
  border-width: 0px;
}
i.color5 {
  background: orange!important;
  border-width: 0px;
}

