footer {
  // bottom: 0;
  // left: 50%;
  // position: fixed;
  // right: 0;
  // z-index: -99999;
  // width: calc( 100% - 100px);
  // transform: translateX(-50%);
  position: relative;
  .footer_item {
    padding-bottom: 40px;
    h3 {
      padding-bottom: 30px;
      text-transform: uppercase;
    }
    p {
      padding-bottom: 25px;
      padding-right: 100px;
    }
    @include tablet {
      padding-right: 0px;
    }
    .outline-icon {
      margin-right: 8px;
      line-height: 40px;
      width: 40px;
      height: 40px;
      &:hover {
        background-color: $primary-color!important;
        border-color: $primary-color!important;
        color: $white-color;
      }
    }
  }
}

//Links
ul.footer_item-category,
.footer_item-service {
  li {
    a {
      display: block;
      position: relative;
      color: $text-color;
      font-size: 14px;
      margin-bottom: 15px;
      padding-left: 15px;
      @include tablet {
        font-size: 13px;
        margin-bottom: 15px;
      }
      @include desktops {
        font-size: 14px;
        margin-bottom: 20px;
      }
      &:before {
        position: absolute;
        content: "\f105";
        font-family: 'FontAwesome';
        left: 0;
        @include transition(color 0.3s ease);
      }
      &:hover {
        color: $primary-color;
        opacity: 1!important;
        text-decoration: underline;
        &:before {
          color: $primary-color;
        }
      }
    }
    &:last-child a {
      margin-bottom: 0px;
    }
  }
}

//location
.footer_item-location {
  padding-right: 100px;
  @include tablet {
    padding-right: 0px;
  }
  li {
    color: $text-color;
    margin-top: 20px;
    font-size: 14px;
    padding-left: 25px;
    @include tablet {
      font-size: 13px;
    }
    @include desktops {
      font-size: 14px;
    }
    &:first-child {
      padding-left: 0px;
      margin-top: 0px;
    }
    a {
      color: $text-color;
      &:hover {
        color: $primary-color;
      }
    }
    i {
      display: inline-block;
      position: absolute;
      left: 15px;
      margin-top: 4px;
    }
  }
}

.footer_item-newsletter {
  padding-left: 0px;
  @include desktops {
    padding-left: 15px;
  }
  p {
    margin-top: 0px;
    color: $text-color;
  }
  form {
    position: relative;
    input {
      background-color: transparent;
      font-size: 14px;
      color: $title-color;
      margin-bottom: 15px;
      width: 65%;
      display: inline-block;
      &:active,
      &:focus {
        background-color: transparent;
      }
    }
    .btn {
      width: 35%;
      float: right;
      padding: 0px;
      height: 50px;
      line-height: 50px;
    }
  }
}

.footer_item-payicon {
  display: block;
  margin-top: 10px;
  li {
    width: auto;
    margin-right: 5px;
    display: inline-block;
    img {
      max-width: 100%;
    }
    &:last-child {
      margin-right: 0px;
    }
  }
}

// Footer bottom - copyright area
.copyright {
  hr {
    margin-bottom: 0px!important;
  }
  p {
    padding: 25px 0;
    line-height: 1;
  }
}

// Back to Top Button
button.back-to-top {
  margin: 0 !important;
  padding: 0 !important;
  background: #fff;
  height: 0px;
  width: 0px;
  overflow: hidden;
  color: transparent;
  clear: both;
  visibility: hidden;
  position: absolute;
  cursor: pointer;
  display: block;
  border: none;
  right: 30px;
  bottom: 70px;
  font-size: 0px;
  outline: 0 !important;
  z-index: 99;
  transition: all .3s ease-in-out;
  @include desktops {
    right: 75px;
  }
}

button.back-to-top:hover,
button.back-to-top:active,
button.back-to-top:focus,
{
  outline: 0 !important;
}

button.back-to-top::before {
  content: "\f077";
  font-family: "FontAwesome";
  display: block;
  vertical-align: middle;
  margin: -5px 0 auto;
}

button.back-to-top.show {
  display: block;
  background: $primary-color;
  color: $white-color;
  font-size: 16px;
  right: 15px;
  bottom: 42px;
  height: 45px;
  width: 45px;
  visibility: visible; 
  @include desktops {
    right: 0px;
  }
}