/*======== 2.14 PAGINATION ========*/ 
.pagination {
    display: inline-block;
    .page-item {
        margin-top: 35px;
        display: inline-block;
        a.page-link {
            border-radius: 0px!important;
            color: $text-color;
            font-size: 15px;
            height: 45px;
            min-width: 45px;
            line-height: 26px;
            overflow: hidden;
            text-align: center;
            border: none;
        }
        &.active, &:hover {
            a.page-link { 
                background-color: $primary-color;
                color: $white-color;
            }
        }
        &:last-child {
            a.page-link {
                margin-right: 0px;
            }
        }
    }
}

.diamond-pagination .page-item {
	a.page-link {
		@include transform(rotate(45deg));
		margin: 10px;
		font-weight: 400;
		@include tablet {
			margin: 15px;
		}
		span, i {
			display: inline-block;
			@include transform(rotate(-45deg));
		}
	}
	.page-item {
		&:last-child {
			a {
				margin: 10px;
				@include tablet {
					margin: 15px;
				}
			}
		}
	}
}