/*======== 2.2 BUTTONS ========*/ 
.btn {
  display: inline-block;
  font-weight: 400;
  height: 50px;
  line-height: 48px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  padding: 0 27px;
  font-size: 13px;
  letter-spacing: 1.15px;
  border-radius: 0;
  text-transform: uppercase;
  @extend .unselectable;
  @include transition(background-color .15s ease-in-out, border-color .15s ease-in-out, color .15s ease-in-out,  box-shadow .15s ease-in-out);   
}
.btn:hover,
.btn:focus, 
.btn:active,
.btn.active {
  outline: none!important;
  background-image: none!important;
  text-decoration: none!important;
  box-shadow: none!important;
}
.btn[disabled],
.btn.disabled {
  cursor: not-allowed;
  opacity: .60;
  pointer-events: none;
}

.btn-primary {
  color: $white-color!important;
  background-color: $primary-color;
  border: 1px solid $primary-color;
  &:hover, &:focus, &.active, &:active {
    background-color: rgba($primary-color, 0.9)!important;
    border-color: $primary-color!important;
  }
}
.btn-secondary {
  background-color: $secondary-color;
  border-color: $secondary-color;
  &:hover, &:focus, &.active, &:active{
    background-color: rgba($secondary-color, 0.9)!important;
    border-color: $secondary-color!important;
  }
}
.btn-success {
  background-color: $success;
  border-color: $success;
  &:hover, &:focus, &.active, &:active{
    background-color: rgba($success, 0.9)!important;
    border-color: $success!important;
  }
}
.btn-warning {
  background-color: $warning;
  border-color: $warning;
  &:hover, &:focus, &.active, &:active{
    background-color: rgba($warning, 0.9)!important;
    border-color: $warning!important;
  }
}
.btn-danger {
  background-color: $danger;
  border-color: $danger;
  &:hover, &:focus, &.active, &:active{
    background-color: rgba($danger, 0.9)!important;
    border-color: $danger!important;
  }
}

.btn-rounded {
  border-radius: 4px;
}
.btn-pill {
  border-radius: 100px;
}
// ========== Button Variations Classes =========
.btn-outline-primary {
  background-color: transparent;
  border-color: $primary-color;
  color: $primary-color;
  &:hover, &:focus, &.active, &:active {
    background-color: $primary-color!important;
    border-color: $primary-color!important;
    color: $white-color;
  }
}
