// ========= Button Sizes ==========
.btn-sm {
  padding: 0px 18px;
  font-size: 12px;
  height: 36px;
  line-height: 34px;
  letter-spacing: 0px;
}
.btn-lg {
  height: 80px;
  line-height: 78px;
  padding: 0 50px;
  font-size: 16px;
  letter-spacing: 4px;
}

