// Products ===============
.filterArea {
  margin-top: 45px;
  margin-bottom: 30px;
  @include tablet {
    margin-top: 0px;
  }
  .col-form-label {
    float: left;
    margin-right: 10px;

  }
  .selectOptions {
    display: table;
    p {
      @include tablet {
        display: table-cell;
        vertical-align: middle;
        padding-top: 5px;
        float: left;
      }
    }
  }
  .selectOptions .sbHolder, .selectOptions .sbSelector {
   width: 150px!important;
   height: 40px!important;
   line-height: 40px!important;
   display: inline-block;
   color: #222!important;
   margin-bottom: 10px;
   @include tablet {
    width: 200px!important;
    display: table-cell;
    vertical-align: middle;
    margin-right: 15px;
  }
}
.selectOptions .sbOptions a {
  font-size: 12px;
  padding: 8px 10px;
}
.selectOptions .sbHolder>a.sbToggle:after {
  color: #222!important;
  right: 15px;
}
.btn-grid, .btn-list {
  background: transparent;
  border: 1px solid #dedede;
  display: inline-block;
  text-align: center;
  color: $primary-color;
  height: 40px;
  width: 40px;
  line-height: 40px;
  cursor: pointer;
  &:hover, &.active {
    background-color: $primary-color;
    color: $white-color;
    border: 1px solid $primary-color;
  }
}
}

.shop-4-column,
.shop-3-column,
.shop-left-sidebar,
.shop-right-sidebar {
  .product_card {
    margin-bottom: 45px;
  }
}
.product_card, .product-media {
  .badge.sale {
    position: absolute;
    top: 10px;
    bottom: unset;
    left: 10px;
    padding: 10px 15px;
    background: $secondary-color;
    color: $white-color;
    font-size: 14px;
    font-weight: 500;
    z-index: 10;
    border-radius: 0px;
    text-transform: uppercase;
    @include tablet {
      font-size: 11px;
      padding: 8px 10px;
    }
    @include desktops {
      font-size: 14px;
      padding: 10px 15px;
    }
  }
}
// Product card ================
.product_card, .product-media {
  .product-action_btn {
    cursor: pointer;
  }
  .btn-wishlist.active {
    color:tomato!important;
  }
}
// Product List ================
.product-media {
  position: relative;
  margin-bottom: 40px;
  .product-media_img {
    min-width: 100%;
    display: block;
    margin-right: 0px;
    @include tablet {
      min-width: 30%;
      margin-right: 25px;
    }
    img {
      max-width: 100%;
    }
  }
  .product-media_body {
    min-width: 100%;
    display: block;
    padding-top: 15px;
    @include tablet {
      padding-top: 0px;
      min-width: 65%;
    }
    @include desktops {

    }
    .product_price {
      font-size: 16px;
    }
    p {
      padding: 30px 0 25px;
    }
    .product-action_btn {
      border: 1px solid rgb(229, 229, 229);
      line-height: 43px;
    }
  }
}
.shop-list-no-sidebar {
  .product-media {
    .product-media_img {
      min-width: 100%;
      display: block;
      margin-right: 0px;
      @include tablet {
        min-width: 20%;
        margin-right: 25px;
      }
    }
  }
}
// Single Product =================
.product_thumbnail {
  img {
    max-width: 100%;
  }
  li {
    margin-bottom: 20px;
  }
  .carousel-item {
    height: auto;
  }
}

.single_product-details {
  h2 {
    margin-bottom: 5px;
    margin-top: 30px;
    @include tablet {
      margin-top: 0px;
    }
  }
  .price.price-lg {
    font-size: 32px;
    display: block;
    margin: 10px 0 30px;
    color: #222;
  }
  .square{
    width: 35px;
    height: 35px;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  .active {
    .square{
      border: 2px solid $primary-color;
      border-radius: 2px;
    }
  }
}

.single_product-details li:first-child {
  padding-left: 0px;
}
.single_product-details li:last-child {
  padding-right: 0px;
}

.single_product-rating {
  display: inline-block;
  margin-bottom: 8px;
  li {
    padding: 0 2px;
    font-size: 16px;
    color: #ffcc00;
  }
}
.product_review-count {
  display: inline-block;
  padding-left: 10px;
}

.single_product-details h6 {
  font-size: 17px;
  margin-bottom: 15px;
}
.single_product-details form ul {
  display: table;
  margin-bottom: 10px;
  li {
    display: table-cell;
    padding: 0 5px;
    a {
      color: $text-color;
    }
  }
  li.active .square {
    color: $primary-color;
  }
}

.single_product-details form {
  margin-bottom: 30px;
  h6 {
    margin-top: 30px;
  }
  .btn {
    height: 50px;
    line-height: 48px;
  }
}

.single_product-details {
  strong {
    margin-right: 10px;
  }
  .category {
    padding: 0px;
    color: $text-color;
    font-size: 15px;
    &:hover {
      color: $primary-color;
    }
  }
  .plain-icon {
    width: auto;
    height: auto;
    padding: 0 7px;
    &:hover {
      color: $primary-color;
    }
  }
  .btn-wishlist {
    color: $primary-color;
    font-size: 17px;
    font-weight: 500;
    margin-left: 10px;
    display: inline-block;
  }
}
// increment Button
.cart_items {
  overflow-x: hidden; 
}
.count-input {
  position: relative;
  width: 110px;
  display: inline-block;
  margin-right: 15px;
  margin-top: 20px;
}

.count-input .incr-btn {
  color: $text-color;
  display: block;
  font-size: 28px;
  font-weight: 300;
  height: 50px;
  line-height: 48px;
  position: absolute;
  right: 0;
  text-align: center;
  text-decoration: none;
  top: 0;
  width: 36px;
}

.count-input .incr-btn:first-child {
  left: 0;
  right: auto;
}
.count-input input {
  -moz-appearance: none;
  background: transparent;
  border: 1px solid #f0f0f0;
  border-radius: 0;
  height: 50px;
  font-weight: 700;
  color: #3b3b3b;
  text-align: center;
  width: 100%;
  line-height: 48px;
}

// Product Review Tab ==============================
.existing-review .media .media-body {
  h6 {
    display: inline-block;
    font-weight: 500;
  }
  ul.list-inline {
    text-align: right;
    float: right;
    li {
      padding: 0 0px;
      i {
        color: $primary-color;
      }
    }
  }
  p {
    padding-bottom: 10px;
    @include tablet {
      padding-right: 30px;
    }
    @include desktops {
      padding-right: 60px;
    }
  }
} 
.product-desc {
  .nav-tabs {
    text-align: center;
    @include tablet {
      text-align: left;  
    } 
    .nav-item .nav-link.active:before {
      bottom: 6px;
      @include tablet {
          bottom: -1px;
      } 
    }
  }
  .tab-content {
    border: none;
  }
}
.existing-review .rd_media {
  padding-bottom: 15px;
  padding-right: 0px;
  @include tablet {
    padding-right: 30px;
  }
  @include desktops {
    padding-right: 60px;
  }
}
.rating_input {
  border-top: 1px solid #e5e5e5;
  padding-top: 40px;
  margin-top: 30px;
  @include tablet {
    padding-top: 0px;
    margin-top: 0px;
    border-top: none;
    padding-left: 30px;
    border-left: 1px solid #e5e5e5;
  }
  @include desktops {
    padding-left: 60px;
  }
  p {
    margin-bottom: 30px;
  }
  h6 {
    margin-bottom: 15px;
    font-weight: 500;
  }
  input.form-control, textarea.form-control, select.form-control {
    border-color: #e5e5e5!important;
  }
}
.rating_input .br-wrapper {
  margin-bottom: 30px;
  a {
    font-size: 16px;
  }

  .br-theme-fontawesome-stars-o .br-widget a.br-selected:after {
    color: $primary-color!important;
  }
  .br-theme-fontawesome-stars-o .br-widget a:after {
    color: $text-color!important;
  }

}
//size-guide ===============
.size-guide {
  .list-styled {
    margin-bottom: 25px;
    li {
      padding-top: 8px;
    }
    li:first-child {
      padding-top: 0px;
    }
  }
  .list-styled p i {
    font-size: 10px;
    margin-right: 10px;
  }

  .table-bordered td, .table-bordered th {
    border-color: $border-color;
    color: $title-color;
    background: $white-color;
    text-align: center;
  }
}
