// Blog Page styles ===============
.blogs {
  .card {
    margin-bottom: 50px;
  }
}
.social-share {
  .outline-icon {
    background-color: transparent;
    &:hover {
      color: $white-color;
      background: $primary-color;
      border-color: $primary-color;
    }
    &:focus, &:active {
      outline: none;
    }
  }
}
.post_contents {
  .post-title {
    padding-bottom: 10px;
  }
  .post-admin {
    padding-bottom: 32px;
  }
  img {
    margin-bottom: 50px;
  }    
}
.post_contents .post-admin a , .post_contents .post-admin span {
  position: relative;
  display: inline-block;
  color: #222222;
  padding-right: 24px;
}
.post_contents .post-admin a:after, 
.post_contents .post-admin span:after {
  content: '/';
  position: absolute;
  right: 6px;
  color: #848484;
  top: 1px;
}

.post_contents .post-admin a:last-child:after {
  content: '';
}

/*============= Comment area style =============*/
.comment-wrapper {
  margin-top: 50px;
  padding-bottom: 70px;
  .rd_media {
    display: block;
  }
}
.blog-single {
  .rd_media {
    .rd_media-left, .rd_media-right {
      vertical-align: top;
    }
    .rd_media-left {
      width: 50px;
      @include tablet {
        width: 80px;
      }
    }
  }
}
.post_comment {
  padding-top: 25px;

  h6 {
    margin-bottom: 10px;
  }
  span {
    margin-bottom: 15px;
    display: inline-block;
  } 
  p {
    margin-bottom: 20px;
  }
  .post_comment-reply {
    margin-left: 65px;
    margin-top: 25px;
    @include tablet {
      margin-left: 100px;
      margin-top: 45px;
    }
    p {
      margin-bottom: 0px;
    }
  }
  .btn {
    color: $primary-color;
    font-size: 12px;
    padding: 0px;
    font-weight: 400;
    height: 30px;
    padding: 0 15px;
    line-height: 30px;
    border-radius: 2px;
    border: 1px solid $primary-color;
    cursor: pointer;
    &:hover {
      box-shadow: none;
      background-color: $primary-color;
      color: $white-color;
    }
  }
}
.comment_box {
  .btn-primary {
    cursor: pointer;
  }
}