/*======== 2.15 PROGRESSBAR ========*/
.progress {
	border-radius: 0px;
  margin-bottom: 10px;
}

.progressText {
	font-size: 15px;
}
.progress-bar{
   background-color: $primary-color;
   text-align: left;
  border-radius: 0px;	
}	
.progress-bar-primary {
    background-color: $primary-color;
}
.progress-bar-success {
    background-color: $success;
}
.progress-bar-info {
    background-color: $info;
}
.progress-bar-warning {
    background-color: $warning;
}
.progress-bar-danger {
    background-color: $danger;
}

.circle1 {
  margin-top: 30px;
  position: relative;
  strong {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    font-size: 30px;
    margin-top: -20px;
    color: $title-color;
  }
  span.text {
    display: block;
    color: $title-color;
    font-size: 16px;
    margin-top: 15px;
    text-transform: uppercase;
  }
}