/*======== 1.2 COMMON CLASSES ========*/

.form-control:focus{
  box-shadow: none;
}
//========== Common element style ==========
a {
  text-decoration: none;
  @include transition(all 0.3s ease-in-out);
  &:hover, &:focus, &:active{
    box-shadow: none;
    outline: none;
    text-decoration: none;
  }
}
.main-wrapper {
  background: $white-color;
  @include desktops {
    padding: 0 50px;
  }
  .container-fluid {
    @include desktops {
      padding: 0px;
    }
  }
}
// Body boxed ========
body.boxed {
  margin:0 auto;
  position: relative;
    @include desktops {
    max-width: 970px;
  }
  @include large-desktops {
    max-width: 1260px;
  }
  &.scroll-fixed {
    .navbar-sticky {

    }
  }
}
body.boxed header {

    @include desktops {
    max-width: 970px;
  }
  @include large-desktops {
    max-width: 1260px;
  }
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.overlay-shadow:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba($primary-color, 0.3);
}
// bredcrumb section
.list-inline li {
    display: inline-block;
    padding: 0 8px;
}
.pager {
  li, a {
    color: $primary-color;
    position: relative;
    letter-spacing: 1px;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    i {
      margin-right: 3px;
    }
  }
}

.pager li:after {
  position: absolute;
  content: '/';
  top: 2px;
  right: -5px;
  font-size: 16px;
}
.pager li:last-child:after {
  display: none;
}
::selection {
  background-color: $primary-color;
  color: $white-color;
}
//========== Helper Classes ========== 
.unselectable {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.text-primary {
  color: $primary-color!important;
}
.text-secondary {
  color: $secondary-color!important;
}
.text-white {
  color: $white-color!important;
}
.bg-primary {
  background-color: $primary-color!important;
}
.bg-secondary {
  background-color: $secondary-color!important;
}
.bg-white {
  background-color: $white-color!important;
}

.bg-white {
  background-color: $white-color;
}
.bg-dark {
  background-color: $dark-navy;
}
.bg-water {
  background-color: transparent;
}
.bg-sand {
  background-color: $sand;
}
.info {
  color: #24bac3;
}
.warning {
  color: #f09725;
}

// ========== Section Title ==========
.section_title {
    padding: 0px 0 30px;
    position: relative;
    @include desktops {
      padding: 0px 0 50px;  
    } 
}
.section_title h2 {
    position: relative;
    letter-spacing: 4px;
}

// ========== Section padding ==========
.section-padding {
  padding-top: 60px;
  padding-bottom: 60px;
  @include desktops {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
.section_top-padding {
  padding-top: 60px;
  @include desktops {
    padding-top: 80px;
  }
}
.section_bottom-padding {
  padding-bottom: 60px;
  @include desktops {
    padding-bottom: 80px;
  }
}
// ========== Media Custom Styles ==========
.rd_media{
  position: relative;
  display: table;

  .rd_media-left {
    display: table-cell;
    vertical-align: middle;
    font-size: 30px;
    img {
      max-width: 100%;
    }
  }
  .rd_media-right {
    display: table-cell;
    vertical-align: middle;
    padding-left: 15px;
    @include tablet {
      padding-left: 25px;
    }
  }
}
.btn-primary:not([disabled]):not(.disabled):active {
  box-shadow: none;
}
