/*======== 2.16 TABS ========*/ 
.nav-tabs {
  border-bottom: 1px solid $border-color;
  .nav-item {
    padding: 0px;
    margin-right: 5px;
    @include tablet {
      // margin-bottom: -1px;
    } 
    .nav-link {
      border-radius: 0px;
      color: $text-color;
      background-color: $sand;
      position: relative;
      margin-right: 0px;
      border-bottom: 0px;
      padding: 10px 15px;
      font-size: 17px;
      &.active {
        color: $title-color;
        background-color: $white-color;
        &:before {
          position: absolute;
          content: '';
          width: 100%;
          height: 2px;
          top: -1px;
          left: 0;
          background-color: $primary-color;
        }
      }
    }
    &:last-child {
      .nav-link {
        margin-right: 0px;
      }
    }
  }
  &.no-bg {
    .nav-item {
      .nav-link {
        background-color: transparent;
        border: 1px solid transparent;
        &.active {
          border-color: $primary-color;
          background-color: $primary-color;
          color: $white-color;
        }
      }
    }
  }
  &.nav-pills {
    .nav-item {
      .nav-link {
        &.active {
          border-color: $primary-color;
          background-color: $primary-color;
          color: $white-color;
          &:before {
            display: none;
          }
        }
      }
    }
  }
}
.tab-content {
  border: 1px solid;
  border-color: $border-color;
  border-top: none;
  padding: 20px;

}
.nav-tabs {
  .nav-item {
    @include tablet {
      margin-bottom: -1px;
    }
  }
}


// Border bottom styled nav tabs ===============================
.nav-tabs.bar-tabs {
  flex-direction: row;
  .nav-item {
    margin-bottom: 0px;
  }
  .nav-link {
    border-radius: 0px;
    position: relative;
    color: $text-color;
    display: inline-block;
    padding-bottom: 15px!important;
    background-color: transparent!important; 
    margin-right: 0px;
    border: none!important;
    @include transition(all 0.3s ease);
    &.active {
      color: $primary-color;
      &:before {
        top: auto;
        bottom: -1px;
      }
    }
  }
}

.nav.nav-tabs {
  flex-direction: column;
  @include tablet {
    flex-direction: unset;
  }
}

// ==================== Vertical Tabs =====================
.nav-stacked {
  -webkit-flex-direction: column!important;
  -ms-flex-direction: column!important;
  flex-direction: column!important;
}
.nav-tabs.nav-stacked {
  margin-right: 0;
  border-bottom: 0;
  border-right: none;
  @include tablet {
    border-right: 1px solid $border-color;
    padding-right: 0;
  }
}
.nav-tabs.nav-stacked {
  .nav-item {
    margin: 0 !important;
    float: none !important;
    display: block !important;
    .nav-link {
      padding: 10px 15px;
      margin-right: 0px;
      border-right: 0px;
      i {
        padding-right: 10px;
      }
      border: 1px solid $border-color;
      border-bottom: 0px;
      margin-right: -1px;
      &.active {
        border-right-color: transparent;
        @include tablet {
          &:before {
            top: 0;
            width: 2px;
            height: 100%;
            bottom: 0px;
          }
        }
      }
    }
    &:last-child {
      .nav-link {
        border-bottom: 1px solid $border-color; 
      }
    }
  } 
}
