/* =========== 1.1 RESET =========== */
* {
  outline: 0;
  margin: 0;
  padding: 0;
}
html, * {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Font faces url*/
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600');

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: $text-color;
  font-weight: 400;
}
ul{
  margin:0;
  padding:0;
  li{
    list-style-type:none;
  }
}

p{
  font-family: 'Open Sans', sans-serif;
  color: $text-color;
  font-weight: 400;
  margin-bottom: 0;
  @include mobile-xs {
  font-size: 13px;
  line-height: 18px;
  }
  @include tablet {
  font-size: 15px;
  line-height: 26px;
  }
}
h1, h2, h3, h4, h5, h6{
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  font-weight: 500;
  color: $title-color;
}
h1 {
  font-size: 40px;
  line-height: 1.1;
}
h2{
  font-size: 28px;
  line-height: 1.1;
  @include tablet {
    font-size: 32px;
  }
}
h3{
  font-size: 20px;
  line-height: 1.1;
  @include tablet {
    font-size: 26px;
  }
}
h4{
  font-size: 22px;
  line-height: 1.1;
}
h5{
  font-size: 20px;
  line-height: 1.1;
}
h6{
  font-size: 17px;
  line-height: 1.1;
}