/* ================================================
--> 3. HEADER
================================================ */
.header {
  position: relative;
  background: $white-color;
}
/* Fixed navber when scroll up */
.up-scroll {
  .navbar-scrollUp {
    transform: translateY(-100%);
    transition: .4s ease-in-out;
  }
}
nav.navbar {
  transition: .4s ease-in-out;
  z-index: 1010;
  padding: 20px 15px;
  margin: 0 auto;
  @include desktops {
    padding: 20px 50px;
  } 
  &.navbar-sticky {
    position: fixed;
    top: 0;
    width: 100%;
    background: $white-color;
    animation-name: fadeInDown;
    border-bottom: 1px solid $border-color;
    @include desktops {
      padding-left: 50px;
      padding-right: 50px;
    } 
  }
  .navbar-toggler:focus {
    outline: none;
  }
}
body.boxed .is-visible, 
body.boxed .navbar-sticky {
  @include desktops {
    max-width: 970px;
  }
  @include large-desktops {
    max-width: 1260px;
  }
}
.navbar-toggler {
  padding: 5px 8px;
  padding-right: 0px;
  border: none;
  cursor: pointer;
  &:focus, &:active {
    outline: none;
  }
}
.navbar-nav.site_nav {
  padding-top: 15px;
  @include desktops {
    padding-top: 0px;
  }
  .nav-item {
    a.nav-link {
      display: block;
      font-weight: 600;
      padding: 10px 15px;
      font-size: 14px;
      color: $primary-color;
      text-transform: uppercase;
            // @include tablet {
            //     font-size: 12px;
            //     padding: 20px 12px;
            //     text-transform: capitalize;
            // }
            @include desktops {
              font-size: 14px;
              padding: 0px 18px;
              text-transform: uppercase;
            }
            @include large-desktops {
              padding: 15px 20px;
            }
          }
          &.active {
            a.nav-link {
              color: $text-color;
            }
          }

        // ======= Dropdown options =======
        &.dropdown {
          .dropdown-toggle {
            &:after {
              content: '\f107';
              font-family: 'FontAwesome';
              border: 0px;
              margin-left: 0px;
              vertical-align: 0;
              font-weight: 700;
              right: 15px;
              position: absolute;
              @include desktops {
                position: relative;
                right: unset;
              }
            }
          }
          &.show {
            .dropdown-toggle {
              &:after {
                content: '\f106'
              }
            }
          }
        }
        // dropdown-item container
        .dropdown-menu {
          background: $primary-color;
          border-radius: 0px;
          padding: 10px 20px;
          margin: 0;
          min-width: 228px;
          @include desktops {
              margin-top: 0px;
          } 
          a {
            color: #c3c3c3;
            &:hover {
              background: transparent;
              color: $white-color;
            }
          }
          li {
            a {
              color: rgba(255,255,255,0.7);
              padding: 10px 5px;
              font-size: 14px;
              display: block;
              &:hover {
                color: rgba(255,255,255,1)
              }
            }
            &:last-child {
              border-bottom: 0px;                
            }
          }
          &.show {
            @include desktops {
              -webkit-animation: dropdown-show 0.3s cubic-bezier(0.43, 0.21, 0, 1.73);
              animation: dropdown-show 0.3s cubic-bezier(0.43, 0.21, 0, 1.73);
            }
          }
        }
        //Mega Menu
        &.dropdown.mega_dropdown {
          position: relative;
          @include desktops {
            position: static;
          }
          .dropdown-menu {
            padding: 20px 5px;
            position: relative;
            @include desktops {
              position: absolute;
              margin-top: -20px !important;
              width: calc(100% - 100px);
              top: 100%;
              left: 50px;
              table-layout: fixed;
              flex-direction: row;
              justify-content: space-between;
            }
            ul {
              @include desktops {
                border-right: 1px solid rgba(100,100,100,0.3);
              } 
            }
            &.show {
              display: block;
              @include desktops {
                display: flex;
                -webkit-animation: dropdown-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
                animation: dropdown-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
              }
            }
            
            h4 {
              color: $white-color;
            }
          }
          .promo-content {
            overflow: hidden;
            .owl-item img {
              max-width: 100%;
              display: inline-block;
              margin: 0 auto;
            }
          }
          .promo-content-details {
            position: absolute;
            background: rgba($primary-color, 0.35);
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 5;
            text-align: center;
            @include tablet {
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;    
            } 
          }
        }
      }
    //second level Dropdown menu
    li.has-children {
      position:  relative;
      > a {
        position: relative;
        display: block;
        &:after {
          position: absolute;
          content: '\f107';
          font-family: 'FontAwesome';
          border: 0px;
          margin-left: 0px;
          vertical-align: 0;
          font-weight: 700;
          right: 15px;
          @include desktops {
            content: '\f105';
          }
        }
      }
    }
    
    li.has-children ul.sub-menu {
      display: block;
      position: relative;
      top: 5px;
      left: 0;
      width: 100%;
      margin-top: -11px !important;
      padding: 0px 15px !important;
      background-color: $primary-color;
      @include desktops {
        display: none;
        position: absolute;
        padding: 10px 15px !important;
        border: 1px solid $border-color;
        top: 0;
        left: 100%;
        width: 200px;
      }
    }
    
    li.has-children:hover ul.sub-menu {
      @include desktops {
        display:  block;
        -webkit-animation: dropdown-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        animation: dropdown-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      }
    }
  }


/* site search */

.site_search {
  background: $title-color;
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  z-index: 1010;
  transition: all 0.5s ease;
  @include tablet {
    top: 80px;
  }
  @include desktops {
    width: calc(100% - 100px);
    left: 50px;
  } 
  .form-control {
    width: 100%;
    color: $white-color;
    background: transparent;
    border: none;
    vertical-align: middle;
    margin: 0;
    font-size: 15px;
    min-height: 50px;
    @include placeholder {
      color: $white-color !important;
    }
    @include tablet {
      font-size: 22px;
      min-height: 80px;
    }
    &.focus, &:focus, &.active, &:active {
      background-color: transparent!important;
    }
  }
  button {
    position: absolute;
    right: 15px;
    background: transparent;
    color: rgba(255,255,255,0.8);
    border: none;
    border: 0px;
    font-size: 18px;
    cursor: pointer;
    &:focus {
      outline: none;
    }
  }
  &.show {
    opacity: 1;
    visibility: visible;
    -webkit-animation: dropdown-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    animation: dropdown-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
}

//right menu items
.toolbar.navbar-nav {
  flex-direction: row;
  position: absolute;
  right: 40px;
  top: 18px;
  @include desktops {
    position: relative;
    right: 0px;
    top: 0px;
  }
  > .nav-item {
      display: inline-block;
      padding: 0px;
      > a {
       padding: 15px 10px;
       font-size: 18px;
       color: $title-color;
       &:after {
         content: '';
       }
     }
     &.dropdown {
       a:after {
        display: none;
      }
      &.show {
        -webkit-animation: dropdown-show 0.3s cubic-bezier(0.43, 0.21, 0, 1.73);
        animation: dropdown-show 0.3s cubic-bezier(0.43, 0.21, 0, 1.73)
      }
    }
    .dropdown-menu {
      position: absolute;
      left: auto;
      right: -25px;
      @include desktops {
        right: 0;
      }
    }
    &:last-child > a {
      padding-right: 15px;
      @include desktops {
        padding-right: 0px;
      }
    }
  }
}
// User dropdown
ul.dropdown-menu.dropdown-menu-user {
  width: 220px;
  border: 0px solid $border-color;
  @include box-shadow(0, 1px, 7px, rgba(0, 0, 0, 0.1));
  border-radius: 0px;
  margin-top:  -5px;
  padding: 20px 12px;
}
h6.user-name {
  font-size: 16px;
  margin-bottom:  3px;
}
.dropdown-menu-user {
  .rd_media {
    margin-bottom: 10px;
    .rd_media-left {
      width: 44px;
      overflow: hidden;
      border-radius: 8px;
    }
    .rd_media-right {
      padding-left: 15px;
    }
  }
  li {
    display:  block;
    a {
      color:  #848484;
      font-size:  14px;
      line-height:  2;
      &:hover {
        color: $primary-color;
      }
    }
  }
} 

// cart dropdown
sub.badge {
  margin-left: -10px;
  color: #fff;
  font-size: 10px;
  bottom: -6px;
  padding: 3px;
  width: 15px;
  height: 15px;
}
.toolbar.navbar-nav .cart .toolbar_dropdown {
  position: absolute;
  left: auto;
  padding: 20px!important;
  width: 300px;
  border: 0px;
  background: $white-color!important;
  @include box-shadow(0,1px,7px,rgba(0, 0, 0, 0.1));
  @include tablet {
    width: 470px;
    padding: 50px!important;
  }
  .cart-close {
    position: absolute;
    right: 20px;
    top: 10px;
    font-size: 20px;
    cursor: pointer;
    padding: 5px;
    @include tablet {
      font-size: 24px;
    }
  }
  h4 {
    margin-bottom: 10px;
    padding: 0px;
  }
  .toolbar_dropdown-group {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 0px;
    .column {
      a.btn {
        color: $white-color;
        font-size: 13px;
        width: 120px;
        padding: 0 5px;
        text-align: center;
        @include tablet {
          min-width: 175px;
        }
        @include large-desktops {
          padding:  0;
          min-width: 175px;
        }
      }
    }
  }
  
  .dropdown_product-item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    position: relative;
    width: 100%;
    padding: 20px 0;
    border-bottom: 1px solid #e5e5e5;
    &:first-child {
      border-top: 0;
    }
    .dropdown_product-thumb,
    .dropdown_product-info {
      padding: 0px;
      padding-right: 10px;
    }
    .dropdown_product-remove {
      display: block;
      position: absolute;
      right: 0;
      color: $title-color;
      font-size: 13px;
      line-height: 1;
      cursor: pointer;
      top: 50%;
      @include transform(translateY(-50%));
      @include tablet {
        top: 30px;
        @include transform(translateY(0));
      } 
    }
    .dropdown_product-info {
      padding-right: 15px;
      padding-left: 5px;
    }
    .dropdown_product-title {
      display: block;
      color: $title-color;;
      font-size: 16px;
      font-weight: 500;
      padding: 5px 0 10px 0px;
      text-decoration: none;
      &::hover {
        color: $title-color;
      }
    }
    .dropdown_product-details {
      color: $title-color;
      font-size: 14px;
      font-weight: 500;
    }
  }      
}
