/*======== 2.17 TABLES ========*/
.table th, .table td {
  vertical-align: middle;
  border-top: none;
  border-bottom: 1px solid $border-color;
  color: $primary-color;
  padding: 20px 0;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.5;
  @include tablet {  
    font-size: 17px;
    max-width: auto;
  } 
}
.table thead th {
  font-weight: 500;
  border-top: 0px;
  border-bottom: 1px solid $border-color;
}
td {
  font-family: 'Open Sans', sans-serif;
  position: relative;
  h6 {
    font-size: 11px;
    @include tablet {
      font-size: 17px;  
    } 
  }
  .count-input {
    position: relative;
    width: 80px;
    display: inline-block;
    margin: 0px 6px;
    @include tablet {
      margin: 0px;
      width: 110px;  
    }
    input {
      height: 35px;
      line-height: 33px;
      @include tablet {
        height: 50px;
        line-height: 48px;
      }
    }
    .incr-btn  {
      height: 35px;
      line-height: 33px;
      font-size: 20px;
      @include tablet {
        font-size: 28px;
        height: 50px;
        line-height: 48px;
      } 
    }
  }
  .text-primary {
    font-size: 12px;
    @include tablet {
      font-size: 14px;
    } 
  }
  button.close {
    position: relative;
    font-size: 24px;
    right: auto;
  }
  .cart_product-img {
    display: inline-block;
    width: 70px;
    @include tablet {
      width: 100px;
    } 
    img {
      max-width: 80%;
      @include tablet {
        max-width: 100%;
      } 
    }
  }
  &:first-child {
    text-align: left;
  }
  &:last-child {
    text-align: right;
  }
  &:nth-child(2) {
    width: 90px;
    @include tablet {
      width: auto;
    } 
  }
  .btn-table {
    width: 162px;
    border: 1px solid $border-color;
    display: inline-block;
    text-align: center;
    color: $primary-color;
    height: 50px;
    line-height: 48px;
    &:hover, &.active {
      border: 1px solid $primary-color;
      background-color: $primary-color;
      color: $white-color;
    }
  }  
}
.acc-table-options {
  padding-bottom: 20px;
  border-bottom: 1px solid $border-color;
  padding-top: 20px;
  @include tablet {
    padding-top: 0px;
  }
  .edit {
    border: none;
    background: transparent;
    font-size: 16px;
    text-transform: capitalize;
    color: $primary-color;
    cursor: pointer;
    i {
      margin-right: 5px;
    }
  }
}
.acc-table {
  margin-top: 50px;
  border-top: 1px solid $border-color;
  @include desktops {
    border-top: none;
    border-left: 1px solid $border-color;
    padding-left: 30px;
  }
  td {
    font-family: 'Open Sans', sans-serif;
    span {
      color: $text-color;
      display: inline-block;
      font-size: 11px;
      padding: 0 2px;
      @include tablet {
        padding: 5px 0 0;  
        font-size: 11px;
      }
      @include desktops {
        padding: 0px;
        font-size: 14px;
      }  
    }
    button {
      position: relative;
      top: 0%;
      font-size: 40px;
      color: $primary-color;
      font-weight: 300;
      background-color: transparent;
      border: none;
      transform: translateY(0%);
    }
  }
  &.order-table tr, 
  &.ticket-table tr, 
  &.ticket-single-table tr {
    td,th {
      @include tablet {
        font-size: 11px;
      } 
      @include desktops {
        font-size: 14px;
      }
      &:nth-child(3), &:nth-child(4) {
        text-align: center;
      }
      .btn-table {
        height: 30px;
        width: 52px;
        line-height: 28px;
        @include tablet {
          height: 40px;
          width: 104px;
          line-height: 38px;
        } 
      }
    }
    a {
      color: $primary-color;
      font-weight: 700;
      &:hover {
        opacity: 0.7;
      }
    }
    .btn-table {
      &:hover {
        opacity: 1;
        color: $white-color;
      }
      &.active {
        color: $white-color;
      }
    }
  }
  &.ticket-table tr:last-child, 
  &.ticket-single-table tr:last-child {
    td {
      border-bottom: none;
    }
  }
} 