// Custom Tables
.btn-tbl-default {
  letter-spacing: 0px;
  border-color: $border-color;
  font-weight: 600;
  @include transition(all 0.3s ease-in);
  &:hover {
    background-color: transparent!important;
    border-color: $primary-color;
    color: $primary-color;
  }
  .update-icon {
    margin-bottom: -3px;
  }
}