// ========== Element Page Common Styles ========== 
.bredcrumb {
	padding: 50px 0;
	border-top: 1px solid rgba(0,0,0,0.08); 
	border-bottom: 1px solid rgba(0,0,0,0.08); 
	h2 {
		text-transform: uppercase;
		font-weight: 500;
		letter-spacing: 2px;
		margin-bottom: 0px;
	}
	ul.pager {
		margin-top: 15px;
	}
}
.single-block {
	margin-top: 80px;
	border: 0;
	.toggle-btn {
		color: $text-color;
		background: $white-color;
		padding: 10px 10px;
		font-size: 14px;
		border-radius: 4px;
		right: 20px;
		position: absolute;
    top: 50%;
    border: 1px solid rgba(0, 0, 0, 0.08);
		@include transform(translateY(-50%));
		@include tablet {						
      right: 0;
      top: 0;
			position: relative;
			@include transform(translateY(0%));
		}			
  }
  .code_container {
    ul {
      padding: 10px 15px 20px;
      li {
        color: $title-color;
        padding: 5px 15px;
        font-size: 15px;
        line-height: 26px;
        &:before {
          content: "\f10c";
          font-family: FontAwesome;
          display: inline-block;
          font-size: 14px;
          color: $text-color;
          margin-left: -24px; 
          width: 24px; 
        }
      }
    }
    .s{color:#e6db74}
    .nt{color:#f92672}
    .na{color:#a6e22e}
  }
}
/* Elements footer style */
.footer.element-footer {
	padding-top: 80px;
	.copyright p {
		color: $text-color;
	}
}