/*======== 2.19 VIDEOS ========*/ 
.promo-video{
  position: relative;
  width: 100%;
  float: none;
  overflow: hidden;
  max-height: 400px;
  padding-top: 100%;
  &:before {
    background-color: rgba(10,35,59,0);
  }
  @include tablet {
    max-height: 500px;
    width: 100%;
    position: relative;
    margin: 0px;
    padding-top: 60%;
  }
  @include desktops {
    padding-top: 40%;
  }
  .video-button{
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    i {
      position: relative;
      height: 80px;
      width: 80px;
      line-height: 80px;
      font-size: 35px;
      border-radius: 50%;
      display: inline-block;
      color: $white-color;
      padding-left: 10px;
      text-align: center;
      border: 2px solid $white-color;
      @include transition(transform 0.3s ease);
    }
    &:hover {
      i {
        @include transform(scale(1.1));
      }
    }
  }
}
