/*======== 2.4 CARDS ========*/
.card {
  border: 0;
  border-radius: 0;
}
.card-img {
  border-radius: 0px;
}
.card-block {
  padding: 20px 0 0;
}
.card-block a {
  color: $text-color;
  &:hover {
    color: $primary-color;
  }
}
.card-title {
  margin-bottom: 5px; 
}
.card-text {
  margin-bottom: 16px; 
}
// ======== Card with Hover ========
.card-hoverable {
  overflow: hidden;
  .overlay-shadow:after {
    background:  rgba(0,0,0,0.4);
    opacity: 0;
    visibility:  hidden;
    transition: all 0.3s ease;
  }
  .btn-primary {
    position:  absolute;
    top: 50%;
    left:  50%;
    transform: translate(-50%, -50%);
    z-index:  1;
    transition: all 0.3s linear;
    margin-top:  15px;
    opacity:  0;
  }
}
.card-hoverable:hover {
  .overlay-shadow:after {
    visibility: visible;
    opacity:  1;
  }
  .btn-primary {
    margin-top: 0;
    opacity:  1;
  }
}

// ======= Card style One ======== 
.card_style-1 {
  overflow: hidden;
  .card_img {
    @include transition(opacity 0.5s 0s ease-out);
    img {
      max-width: 100%;
    }
  }
  .card-block {
    padding-top: calc(50% - 30px);
    padding-left: 30px;
    padding-right: 30px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    @include transition(all 0.5s ease-out);
    @include tablet {
      padding-top: calc(50% - 50px);
    }

    .card-title {
      padding-top: 0px;
      padding-bottom: 0px;
      text-transform: uppercase;
      @include tablet {
        font-size: 18px;
      }
      @include large-desktops {
        font-size: 23px;
      }
    }
    > span {
      font-size: 15px;
      font-weight: 500;
      letter-spacing: 2px;
      color: $primary-color;
    }
    .btn {
      font-size: 13px;
      opacity: 0;
      @include transform(translateY(20px));
      @include transition(all 0.5s 0.3s ease-out);
    }
    .btn-primary {
      color: $white-color;
    }
    .card_hover-gallery {
      padding: 15px 0 0;
      opacity: 0;
      visibility: hidden;
      transition-delay: 0.5s;
      @include transform(translateY(20px));
      @include transition(all 0.5s 0.2s ease-out);
    }
    .hover-gallery_img {
      margin-bottom: 20px;
      margin-right: 10px;
      &:last-child {
        margin-right: 0px;
      }
      img {
        border: 1px solid rgba(0,0,0,0.08);
        max-width: 100%;
      }
    }
  }
  &:hover {
    .card_img {
      opacity: 0.5;
    }
    .card-block {
      padding-top: 0;
    }
    .card_hover-gallery {
      opacity: 1;
      visibility: visible;
      @include transform(translateY(0px));
    }
    .btn {
      opacity: 1;
      @include transform(translateY(0px));
    }
  }
}

.product-action_btn {
  width: 45px;
  height: 45px;
  display: inline-block;
  background-color: $white-color;
  color: $primary-color;
  text-align: center;
  line-height: 45px;
  font-size: 16px;
  border-radius: 100%;
  border: 1px solid transparent;
  @include transition(all 0.3s ease);
  .cart-svg {
    fill: rgb(0,0,0);
  }
  &:hover {
    color: $white-color;
    background-color: $primary-color;
    .cart-svg {
      fill: rgb(255,255,255);
    }
  }
  &:focus {
    outline: none;
  }
}

// Card style Two
.card_style-2 {
  .card_img {
    img {
      max-width: 100%;
    }
  }
  .card_img-overlay {
    @extend .overlay;
    background-color: rgba(0,0,0,0.2);
    opacity: 0;
    visibility: hidden;
    @include transition(all 0.3s ease-in);

    .overlay-content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: right;
      padding: 20px;
      height: 100%;
      li {
          @include transform(translateX(100px)); 
        a, button {
          @extend .product-action_btn;
        }
        // &:first-child {
        //   @include transition(transform 0.5s ease-out);
        // }
        // &:nth-child(2) {
        //   margin: 5px 0;
        //   @include transform(translateX(100px));
        //   @include transition(transform 0.6s ease-out);
        // }
        // &:last-child {
        //   @include transform(translateX(100px));
        //   @include transition(transform 0.7s ease-out);
        // }
      }
    }
  }
  &:hover {
    .card_img-overlay.effect-1 {
      opacity: 1;
      visibility: visible;

      .overlay-content {
        li {
          // @include transform(translateX(0));
          &:first-child {
            @include transform(translateX(0)); 
            @include transition(transform 0.5s ease-out);
          }
          &:nth-child(2) {
            margin: 5px 0;
            @include transform(translateX(0));
            @include transition(transform 0.6s ease-out);
          }
          &:last-child {
            @include transform(translateX(0));
            @include transition(transform 0.7s ease-out);
          }
        }
      }
    }
  }
}