/*======== Page about us ========*/
.about_img-box1 {
  img {
    width: calc(100% + 80px);
  }
}
.about_img-box2 {
  img {
    width: calc(100% + 80px);
    margin-left: -100px;
    @include box-shadow(6px, 10px, 20px, rgba(0, 0, 0, 0.17));
  }
}

.about_content-box {
  margin-top: 25px;
  @include tablet {
    margin-top: 0px;
  }
  h4 {
    margin-bottom: 5px;
  }
  h2 {
    margin-bottom: 30px;
  }
  > p {
    padding-bottom: 10px;
  }
  .rd_media {
    margin-top: 35px;
  }
  .rd_media-left {
    vertical-align: top;
    font-size: 16px;
    padding-right: 20px;
    text-align: left;
  }
  .rd_media-right p {
    padding-top: 10px;
  }
} 

// Owl Testimonial Carousel ====================
.owl-testimonial-carousel {
  .clients_speech-single {
    p {
      font-family: 'Open Sans', sans-serif;
      font-size: 18px;
      line-height: 32px;
      padding: 35px 0;
    }
  }
  .owl-dots {
    margin-top: 35px;
    position: relative;
    display: block;
    background: transparent;
    .owl-dot {
      width: 55px;
      height: 55px;
      overflow: hidden;
      border-radius: 50%;
      position: relative;
      &:after {
        @extend .overlay;
        content: '';
        background-color: rgba($title-color, 0.2);
      }
      img {
        max-width: 110%;
      }
    }
    .owl-dot.active:after {
      background-color: rgba($title-color, 0);
    }
  }
}
//Store Location
.store_location {
  padding: 80px 25px;
  position: relative;
  
  &.bg-image:before {
    background: rgba(0,0,0, 0.4);
  }
}

.store_location i.fa.fa-map-marker {
  color: #fff;
  font-size: 40px;
  padding-bottom: 20px;
}

.store_location h4 {
  color: #fff;
  margin-bottom: 22px;
}

.store_location address {
  position: relative;
  color: #fff;
}

.store_location address p {
  color: #fff;
  font-size: 18px;
  line-height: 26px;
  padding-bottom: 20px;
}

.store_location a.btn.btn-default.bg-white {
  position: relative;
  color: #222;
}
// Experts ====================
.expert {
  .card {
    .card-title {
      padding-bottom: 0px;
      margin-bottom: 5px;
    }
  }
}
// Page Error ====================
.error {
  border-top: 1px solid $border-color;
  padding: 100px 0;
  @include tablet {
    padding: 150px 0;
  }
  h1 {
    font-size: 150px;
    line-height: 1;
  }
  h2 {
    padding: 10px 0 30px;
  }
  form {
    margin-bottom: 50px;
  }
}
// comingsoon PAGE =====================================
.page-comingsoon {
  padding: 135px 0;
  .path-2 {
    fill: $title-color;
  }
  background-repeat: no-repeat;
  &.bg-image:before {
    background-color: rgba(255, 255, 255, 0.93)
  }
  .form_subscribe {
    position: relative;
    i {
      color: $primary-color;
    }
    input {
      background: $white-color;
      border: 1px solid $sand;
      margin-bottom: 0px;
      @include placeholder {
        color: $text-color;
      }
    }
  }
  .comingsoon {
    h1 {
      margin-top: 80px;
      margin-bottom: 15px;
      font-weight: 500;
      text-transform: uppercase;
    }
  }
  .count-down {
    padding-top: 70px;
    padding-bottom: 70px;
    .table-cell {
      position: relative;
      height: 150px;
    }
    .tab-val {
      width: 150px;
      font-size: 40px;
      height: 150px;
      line-height: 112px;
      font-weight: 500;
      background-color: $primary-color!important;
      border-color: $primary-color;
      color: $white-color;
    }
    .tab-metr {
      margin-top: -65px;
      font-size: 16px;
      color: $white-color!important;
    }
  }
  .form_subscribe {
    margin: 0 0 80px;
  }
}
