/*======== 2.3 BACKGROUNDS ========*/ 
.h300 {
	height: 300px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.bg-gradient-primary {
	background: -moz-linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898; 
 background-blend-mode: multiply,multiply;;
	background: -webkit-linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898; 
 background-blend-mode: multiply,multiply;;
	background: -ms-linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898; 
 background-blend-mode: multiply,multiply;;
	background: linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898; 
 background-blend-mode: multiply,multiply;;
}
.bg-gradient-info {
	background: -moz-linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
	background: -webkit-linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
	background: -ms-linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
	background: linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
}
.bg-gradient-success {
	background: -moz-linear-gradient(180deg, #2af598 0%, #009efd 100%);
	background: -webkit-linear-gradient(180deg, #2af598 0%, #009efd 100%);
	background: -ms-linear-gradient(180deg, #2af598 0%, #009efd 100%);
	background: linear-gradient(180deg, #2af598 0%, #009efd 100%);

}


// Image Background
.bg-image-holder, .bg-image {
	background-size: cover !important;
	background-position: 50% 0 !important;
	@include transition(all 0.3s linear);
	background: $sand;
	position: relative;
}
.bg-image {
	&:before {
	    content: '';
	    position: absolute;
	    top: 0;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    background-color: rgba(0,0,0, 0.70);
	}
}
.bg-fixed {
	background-attachment: fixed;
}
.bg-image {
	.overlay-content {
		position: relative;
		z-index: 5;
	}
}
.bg-video {
	position: relative;
	height: 400px;
	overflow: hidden;
	video {
	    position: absolute;
	    top: 50%;
	    left: 50%;
	    min-width: 100%;
	    min-height: 100%;
	    width: auto;
	    height: auto;
	    z-index: -100;
	    transform: translateX(-50%) translateY(-50%);
	 background: url('//demosthenes.info/assets/images/polina.jpg') no-repeat;
	  background-size: cover;
	  transition: 1s opacity;
	}
}
