/*======== 2.10 GALLERY ========*/
.gallery-item {
  margin-bottom: 30px;
  > a {
    display: block;
    position: relative;
    width: 100%;
    overflow: hidden;
    &:before {
      content: '';
      @extend .overlay;
      background: rgba(0,0,0,0.5);
      opacity: 0;
      @include transition( all .35s ease-in-out);
    }
    &:after {
      display: block;
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      margin-top: -10px;
      @include transform(translateY(15px));
      @include transition( all .35s ease-in-out);
      color: $white-color;
      font-family: FontAwesome;
      font-size: 26px;
      text-align: center;
      content: '\f002';
      opacity: 0;
      z-index: 5;
    }
    &.video-link:after {
      width: 50px;
      height: 50px;
      left: 50%;
      border-radius: 50%;
      margin-top: -25px;
        margin-left: -25px;
      font-size: 22px;
      @include transform(translate(0));
        line-height: 50px;
        padding-left: 5px;
        color: $primary-color;
      background-color: $white-color;
      content: '\f04b';
      opacity: 1;
    }

    > img {
      max-width: 100%;
    }
  } 
  >a {
    &:hover::before {
        opacity: 1;
      }
      &:hover::after {
      @include transform(translateY(0));
        opacity: 1;
      }
  }
}
