// Account Cart page =================
.cart_list-update {
  padding: 0px 0 15px;
  border-bottom: 1px solid $border-color;
  @include tablet {
    padding: 15px 0;
  } 
  input.form-control {
    max-width: 60%;
    margin-right: 15px;
    display: inline-block;
    margin-bottom: 15px;
  }
}
.cart_list-amount {
  padding: 25px 0 30px;
  border-bottom: 1px solid $border-color;
  ul {
    margin-top: 15px;
    padding-top: 15px;
    border-top: 1px solid $border-color;
    li {
      padding-bottom: 15px;
      color: $primary-color;
      font-size: 17px;
    }
  }
  .btn-primary {
    height: 60px;
    line-height: 58px;
    font-size: 13px;
    letter-spacing: 0px;
    padding: 0px;
    @include desktops {
      height: 80px;
      line-height: 78px;
      font-size: 16px;
    } 
  }
}

// Account Wishlist details 
.account_banner .bg-image-holder {
  min-height: 120px;
  background-position: center;
  background-size: cover; 
}

.account_banner-content {
  border: 1px solid $border-color;
  border-top: none;
  padding: 50px 0;
  i {
    margin-right: 7px;
  }
  .btn-secondary-outlined {
    background-color: $white-color!important;
  }
}
.profile-img {
  overflow: hidden;
  width: 150px;
  height: 150px;
  margin-top: -125px;
  border: 5px solid $white-color;
  display: inline-block;
  text-align: center;
  margin-bottom: 15px;
  img {
    max-width: 100%;
  }
}
// Account Style Two Page
.account-style-two {
  .account_banner-content {
    padding: 30px 0;
    border-top: 1px solid $border-color;
    border-left: none; 
    border-right: none; 
    .profile-img {
      width: 100px;
      height: 100px;
      padding-top: 0px;
      margin-bottom: 0px;
    }
    
  }
}
// Account Style Three Page
.account-style-three {
  .account-options {
    margin-top: 0px;
  }
  .wishlist-table {
    @include tablet {
      margin-top: 0px;
    }
  }
}
// Account Options Tab
.account-options {
  margin-top: 50px;
  li {
    a {
      border: 1px solid $border-color;
      border-top: 0px;
      padding: 15px;
      display: block;
      color: $text-color;
      font-weight: 500;
      span {
        width: 30px;
        height: 30px;
        border: 1px solid $border-color;;
        text-align: center;
        line-height: 30px;
        border-radius: 50%;
        margin-top: -5px;
      }
    }
    &:first-child {
      a {  
        border-top: 1px solid $border-color;;
      }
    }
    &.active {
      a {
        background: $primary-color;
        color: $white-color;
        border-color: $primary-color;
      }
    }
  }
  li a i {
    width: 24px;
    display: inline-block;
    text-align: center;
    margin-right: 5px;
  }
  li a .badge {
    width: 80px;
    height: 30px;
    font-weight: 500;
    border-radius: 15px;
    line-height: 20px;
    font-size: 100%;
    .svg {
      margin-top: -3px;
      margin-right: 5px;
    }
  }
}
// All Registration form style
form.register {
  border: 1px solid $border-color;
  padding: 40px 40px;
  .form-group {
    margin-bottom: 20px;
  }
  .media {
    margin-top: 15px;
  }
  label {
    color: $primary-color;
    padding-top: 0px;
    padding-bottom: 0px;
  }
  > p {
    font-size: 14px;
    margin-top: 15px;
    margin-bottom: 15px;
    @include tablet {
      margin-top: 0px;
    }
  }
  .btn-social {
    width: 100%;
    position: relative;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    height: 50px;
    letter-spacing: 0px;
    line-height: 48px;
    z-index: 1;
    text-transform: capitalize;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
    &.facebook {
      color: #46639f;
      border-color: rgba(70,99,159, 0.3);
      &:after {
        background-color: #46639f;
      }
    }
    &.google-plus {
      color: #dd4b39;
      border-color: rgba(214,81,62,0.3);
      &:after {
        background-color: #dd4b39;
      }
    }
    i {
      margin-right: 8px;
    }
    &:hover {
      color: $white-color;
      &:after {
        width: 100%;
      }
    }
    &:after {
        content: '';
        position: absolute;
        z-index: -1;
        width: 0%;
        height: 100%;
        top: 50%;
        left: 50%;
        @include transform(translateX(-50%) translateY(-50%));
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        transition: all 0.3s;
    }
  }
}
// Other account Form style
.acc-table {
  .option-title {
    margin: 10px 0 0;
    padding-bottom: 10px;
    @include desktops {
      margin: 0px;
    } 
  }
  .btn-primary {
    width: 170px;
    height: 50px;
    line-height: 50px;
    padding: 0 5px;
    font-size: 14px;
    letter-spacing: 1.1px;
    @include tablet {
      width: 200px;
      height: 65px;
      line-height: 65px;
      letter-spacing: 2px;
    }
    @include large-desktops {
      width: 270px;
      height: 80px;
      line-height: 80px;
      letter-spacing: 4px;
    } 
  }
}
.acc-table form {
  .form-group {
    margin-bottom: 15px;
  }
  .col-form-label {
    padding-top: 0px;
    padding-bottom: 0px;
    color: $primary-color;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 600;
  }
}
// Feedback area 
.feedback {
  display: block;
  position: relative;
  padding-bottom: 30px;
  padding-left:  66px;
}
.feedback_author-img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height:  50px;
  border-radius: 50%;
  overflow: hidden;
  img {
    display: block;
    width: 100%;
  }
}
.feedback_body {
  position: relative;
  padding: 15px 20px;
  border: 1px solid $border-color;
    // border-radius: 7px;
    background-color: $white-color;
    &::before, &::after {
      position: absolute;
      top: 12px;
      right: 100%;
      width: 0;
      height: 0;
      border: solid transparent;
      content: '';
      pointer-events: none;
    }
    &::before {
      margin-top: -1px;
      border-width: 10px;
      border-color: transparent;
      border-right-color: $border-color;
    }
    &::after {
      border-width: 9px;
      border-color: transparent;
      border-right-color: $white-color;
    }
  }

  .feedback_footer {
    display: table;
    width: 100%;
    padding-top: 10px;
  }

  span.feedback_meta {
    color: #9da9b9;
    font-size: 13px;
  }