/*======== 2.11 ICON STYLES ========*/
.plain-icon, .outline-icon, .fill-icon, .fill-outline-icon {
    position: relative;
    display: inline-block;
    text-align: center;
	transition: all .2s ease-in-out;
	color: $text-color;
}
.icon-xs {
	width: 24px;
	height: 24px;
	font-size: 12px;
	line-height: 24px;
}
.icon-sm {
	width: 30px;
	height: 30px;
	font-size: 14px;
	line-height: 30px;
}
.outline-icon {
	border-style: solid;
	border: solid 1px #eee;
	&.icon-xs {
		width: 30px;
		height: 30px;
		font-size: 12px;
		line-height: 30px;
	}
	&.icon-sm {
		width: 40px;
		height: 40px;
		font-size: 14px;
		line-height: 40px;
	}
}
.fill-icon {
	background-color: #eee;
	&.icon-xs {
		width: 30px;
		height: 30px;
		font-size: 12px;
		line-height: 30px;
	}
	&.icon-sm {
		width: 40px;
		height: 40px;
		font-size: 14px;
		line-height: 40px;
	}
}
.fill-outline-icon {
    padding: 4px;
    background: transparent !important;
    border: solid 1px #eee;
	transition: all .1s ease-in-out;
	&.icon-xs {
		width: 40px;
		height: 40px;
		font-size: 12px;
		line-height: 30px;
	}
	&.icon-sm {
		width: 50px;
		height: 50px;
		font-size: 14px;
		line-height: 40px;
	}
	.fill-outline-icon-inner {
		position: relative;
		display: block;
		overflow: hidden;
		background-color: #eee;
	}
}