$primary-color:#000000;
$secondary-color:#f48770;
$white-color: #ffffff;
$black-color: #000000;
$sand: #f5f5f5;
$text-color:#848484;
$title-color: #000000;
$border-color: #e5e5e5;
$navy : #0a233b;
$dark-navy : #071e33;

$success: #49c32c;
$bg-success: #49c32c;
$info: #439fdb;
$bg-info: #439fdb;
$warning: #eabf38;
$bg-warning: #eabf38;
$danger: #f55c5d;
$bg-danger: #f55c5d;



@function tint($primary-color, $percentage) {
  @return mix($primary-color, transparent, $percentage);
}
@function shade($color, $percentage) {
  @return mix(black, $color, $percentage);
}