/* ======== Hero Slider ======== */
.hero-block {
  .container-fluid {
    @include mobile-xs {
      padding: 0px;
    }
  }
}
.hero-slider {
  .slider_content {
    display: flex;
    @include large-desktops {
      min-height: 700px;
    }      
  }
}
// ======= Theme Banner ======== 
.hero-slider {
  width: 100%;
  overflow: hidden;
  background: #f0f0f0;
  position: relative;  
  &:before, &:after {
    content: '';
    position: absolute;
    background: #e1e1e1;
    border-radius: 100%;
    @include tablet {
      z-index: 1;
      width: 200px;
      height: 200px;
    }
    @include desktops {
        width: 260px;
        height: 260px;
    }
    @include large-desktops {
        width: 350px;
        height: 350px;
    } 
  }
  &:before {
    box-shadow: 7px 7px 15px rgba(255, 255, 255, 0.8);
    top: -100px;
    left: -100px;
    @include desktops {
        top: -130px;
        left: -130px;
    }
    @include large-desktops {
        top: -175px;
        left: -175px;
    } 
  }
  &:after {
    bottom: -100px;
    left: -100px;
    box-shadow: 7px -7px 15px rgba(255, 255, 255, 0.8);
    @include desktops {
      bottom: -130px;
      left: -130px;  
    } 
    @include large-desktops {
      bottom: -175px;
      left: -175px;  
    } 
  }
  .slider_content {
    display: flex;
    @include large-desktops {
      min-height: 700px;
    }
    .slider_content-left {
      width: 0%;
      display: none;
      @include tablet {
        width: 50%;
        display: block;
      }
    }
    .slider_content-right {
      width: 100%;
      min-height: 280px;
      background-position: 100%!important;
      .over-content {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        padding: 0 15px 30px;
        @include tablet {
          position: absolute;
          padding: 0;
        }
      }
      @include tablet {
        min-height: 380px;
        width: 50%;
        .over-content {
          left: -60%;
        }
      }
      @include desktops {
        min-height: 480px;
      }
      @include large-desktops {
        .over-content {
          left: -63%;
        }
      }
      h1 {
        font-size: 26px;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-weight: 500;
        padding-bottom: 5px;
        @include tablet {
          font-size: 35px;
        }
        @include desktops {
          font-size: 44px;
        }
        @include large-desktops {
          font-size: 70px;
          letter-spacing: 4px;
        }
      }
      h2 {
        font-size: 12px;
        text-transform: capitalize;
        letter-spacing: 0px;
        margin-bottom: 5px;
        @include tablet {
          font-size: 16px;
          letter-spacing: 1px;
          margin-bottom: 10px;
        }
        @include desktops {
          font-size: 24px;
          margin-bottom: 15px;
        }
        @include large-desktops {
          letter-spacing: 2px;
          margin-bottom: 30px;
          font-size: 32px;
        }                
      }
      p {
        max-width: 250px;
        font-size: 10px;
        padding-bottom: 15px;
        line-height: 15px;
        @include tablet {
          max-width: 350px;
          font-size: 13px;
          line-height: 20px;
          padding-bottom: 25px;
        }
        @include desktops {
          max-width: 400px;
          font-size: 15px;
          line-height: 26px;
          padding-bottom: 30px;
          letter-spacing: 1.1px;
        }
        @include large-desktops {
          max-width: 460px;
        }
      }
      a.btn-primary {
        width: 130px;
        height: 50px;
        line-height: 50px;
        padding: 0 5px;
        font-size: 14px;
        letter-spacing: 1.1px;
        @include tablet {
          width: 170px;
          font-size: 16px;
          letter-spacing: 2px;  
        } 
        @include desktops {
          width: 200px;
          height: 65px;
          line-height: 65px;
          letter-spacing: 2px;
        }
        @include large-desktops {
          width: 220px;
          height: 80px;
          line-height: 80px;
          letter-spacing: 4px;
        }
      }
    } 
  }

  // Slick Dots custiomizer css
  .owl-dots {
    position: absolute;
    display: block;
    width: auto;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: left;
    left: 15px;
    bottom: 30px;
    background: transparent;
    border-radius: 0;
    @include transform(translateX(0));
    @include tablet {
      bottom: unset;
      text-align: center;
      left: 45px;
      top: 50%;
      @include transform(translateY(-50%));
    }

    .owl-dot {
      position: relative;
      display: inline-block;
      text-align: left;
      width: 27px;
      height: auto;
      margin: 0px;
      padding: 0;
      cursor: pointer;
      margin-right: 10px;
      @include tablet {
        display: block;
        @include tablet {
          margin: 0;
        }
      }
      span {
        font-size: 14px;
        line-height: 26px;
        color: #9e9e9e;
        padding: 0px;
        text-align: left;
        margin-top: 21px;
        display: inline-block;
        font-weight: 600;
        letter-spacing: 2px;
        position: unset;
        padding-bottom: 3px;
      }
      &.active span {
        color: #222222!important;
        &:before {
          background: #222222;
        }
      }
    }
  }
  .owl-dots .owl-dot:first-child span {
    margin-top: 0px;
  }
  .owl-dots .owl-dot span:before {
    font-family: 'FontAwesome';
    position: absolute;
    left: 0;
    top: auto;
    width: 100%;
    height: 2px;
    content: '';
    text-align: center;
    opacity: 1;
    background: #9c9c9c;
    bottom: 0px;
  }
  .owl-dots .owl-dot.active {
    width: 37px;
    @include tablet {
      width: 47px;
    }
  }
}
// ======== Category section ======== 

.category-section {
  overflow-x: hidden;
  .section_title {
    padding: 0px 0 30px;
    position: relative;
    @include tablet {
      padding: 0px 0 40px;  
    } 
    @include desktops {
      padding: 0px 0 60px;  
    } 
  }
  .section_title-category {
    margin: 28px 0 0px;
    font-weight: 500;
    letter-spacing: 2px;
    z-index: 5;
    position: relative;
    @include desktops {
        margin: 35px 0 0px;
    } 
  }
  .section_title:after {
    content: 'CATEGORIES';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: $sand;
    font-size: 70px;
    top: 6px;
    z-index: 0;
    letter-spacing: 8px;
    font-weight: 600;
    @include tablet {
      font-size: 100px;
      top: 10px;
    }
    @include desktops {
      font-weight: 700;  
    } 
  }
  .show-nav-top {
    padding-top: 70px;
    @include desktops {
      padding-top: 90px;  
    } 
  }
}

/*======== Top products ========*/

.product_card {
  margin-bottom: 30px;
  text-align: center;
  @include tablet {
    text-align: left;
    margin-bottom: 0px;
  }

  .card-title {
    font-weight: 600;
    padding-bottom: 0px;
    margin-bottom: 5px;
  }
}


/*======== Call To Action ========*/
.cta_item {
  padding: 60px 15px;
  background-position: 75% 0 !important;
  @include tablet {
    background-position: 100% 0 !important;
  }
  @include desktops {
    padding: 100px 35px;
  }
  @include large-desktops {
    padding: 140px 125px;
    background-position: 100% 0 !important;
  }

  h2 {
    font-size: 20px;
    margin-bottom: 10px;
    text-transform: uppercase;
    @include tablet {
      font-size: 22px;
      margin-bottom: 8px;
    }
    @include desktops {
      letter-spacing: 2px;
      font-size: 26px;
    }
    @include large-desktops {
      font-size: 32px;
    }
  }
  h1 {
    font-size: 26px;
    margin-bottom: 15px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 500;
    @include tablet {
      font-size: 44px;
      margin-bottom: 20px;
    }
    @include desktops {
      font-size: 55px;
    }
    @include large-desktops {
      font-size: 70px;
      margin-bottom: 25px;
      letter-spacing: 4px;
    }
  }
  p {
    font-size: 12px;
    line-height: 18px;
    max-width: 250px;
    margin-bottom: 30px;
    @include tablet {
      font-size: 15px;
      line-height: 26px;
      max-width: 400px;
    }
    @include large-desktops {
      max-width: 480px;
    }
  }
  a.btn-primary {
    width: 130px;
    height: 50px;
    line-height: 50px;
    padding: 0 5px;
    font-size: 14px;
    letter-spacing: 1.1px;
    @include tablet {
      width: 170px;
      font-size: 16px;
      letter-spacing: 2px;  
    } 
    @include desktops {
      width: 200px;
      height: 65px;
      line-height: 65px;
      letter-spacing: 2px;
    }
    @include large-desktops {
      width: 220px;
      height: 80px;
      line-height: 80px;
      letter-spacing: 4px;
    }
  }
}

/*======== Blogs ========*/
.blog-area {
  .card {
    margin-bottom: 30px;
    @include desktops {
      margin-bottom: 0px;
    }
    .card-block {
      h4 {
        @include tablet {
          font-size: 17px;  
        } 
        @include desktops {
          font-size: 22px;
        }
      }
      p {
        @include tablet {
          font-size: 12px;
        }
        @include desktops {
          font-size: 15px;  
        }  
      }
    } 
  }
}
/*======== Benefits ========*/
.benefits {
  padding: 60px 0 20px;
  background: #f8f8f8;
  .rd_media {
    padding-bottom: 40px;
  }
  .rd_media-left {
    min-width: 60px;
    text-align: center;
    @include desktops {
      vertical-align: top;
    }
    i {
      font-size: 50px;
      color: $primary-color;
    }
  }
  .rd_media-right {
    @include tablet {
      padding-left: 18px;  
    } 
    @include desktops {
      padding-left: 20px;  
    } 
    h5 {
      margin-bottom: 5px;
      font-weight: 500;
    }
  }
}
/*======== Brands ========*/
.brand_logo {
  .owl-item img {
    max-width: 100%;
  }
}

#gallery {
  img {
    max-width: 100%;
  }
  .gallery-item {
    margin-bottom: 0px;
  }
  .gallery-item a:after {
    content: "\f16d";
  }
}